<template>
<div class="cardContainer">
<v-card
    class="mx-auto"
    max-width="344"
    outlined
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
          {{category}}
        </div>
        <v-list-item-title class="text-h5 mb-1">
          Download Icon
        </v-list-item-title>
        <v-list-item-subtitle>Download icon from multiple different angles</v-list-item-subtitle>
      </v-list-item-content>

      <v-list-item-avatar
