        size="80"
        color="grey"
        style="display: flex; justify-content: center; align-items: center"
      >
      <img :src="require(`@/assets/${imgUrl}`)" style="width: 60px" alt=""></v-list-item-avatar>
    </v-list-item>

    <v-card-actions>
      <v-btn
