        category: String
    },
    methods:{
        closeDialog(){
            this.$emit("closeButton");
        }
    }
}
</script>

<style scoped>
.cardContainer{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.231);
    display: flex;
    justify-content: center;
    align-items: center;
}
